<template>
  <select class="main-select" v-model="cityToDisplay" @change="this.selectCityToDisplay" name="cityList" id="cityList">
    <option value="" selected disabled>Select city...</option>
    <option v-for="city in cityList" :key="city.id" :value="city.name">{{city.name}}</option>
  </select>
</template>

<script>
import {HTTP} from '@/utils/http-common.js'
export default {
  name: 'city-select',
  props: {
    cityList: Array,
    default: [],
    required: true
  },
  data() {
    return {
      cityToDisplay: '',
    }
  },
  methods: {
    selectCityToDisplay() {
      this.$emit('city-to-display', this.cityToDisplay)
    }
  }
}
</script>
<style scoped lang="scss">
@import "../scss/_vars.scss";

.main-select {
  font-size: 36px;
  font-weight: 500;
  background: #fff;
  border: none;
  padding: 20px;
  color: $dark-grey;
}
</style>
