<template>
  <section>
    <city-select 
      :cityList="cityList" 
      @city-to-display="cityToDisplay($event)" />
    <weather-information 
      :selectedCity="selectedCity" 
      :cityWeatherInformation="cityWeatherInformations" />
  </section>
</template>

<script>
import {HTTP} from '@/utils/http-common.js'
import CitySelect from '../components/CitySelect'
import WeatherInformation from '../components/WeatherInformation'

export default {
  name: 'main-view',
  components: {
    CitySelect,
    WeatherInformation
  },
  props: {
    msg: String
  },
  data() {
    return {
      selectedCity: '',
      cityList: [],
      cityWeatherInformations: [],
    }
  },
  methods: {
    cityToDisplay(cityName) {
      this.selectedCity = cityName
      const city = this.cityList.find(city => city.name === cityName)
      HTTP.get(`${city.id}/weather?date=2019-07-24`).then(response => {
        this.cityWeatherInformations = response.data
      }).catch(e => {
        console.log(e)
      })
    }
  },
  created() {
    // const days = ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday'];
    // const d = new Date('2019-05-24');
    HTTP.get().then(response => {
      this.cityList = response.data
    }).catch(e => {
      console.log(e)
    })
  }
}
</script>
